import React from 'react'
import { toast } from "react-toastify";
import BottomTabs from './BottomTabs'
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader'
import { FiSearch } from 'react-icons/fi'
import cssVar from 'views/mobileView/styles'
import db from 'libs/db'
import AdminServices from 'services/api/AdminServices';
import SliderComponent from 'views/mobileView/components/atoms/SliderComponent';
import { RiskFormat } from 'views/mobileView/components/atoms/RiskFunctions';
import DisplayRecentlyViewed from 'views/mobileView/components/atoms/DisplayRecentlyViewed';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';
import SliderComponent2 from 'views/mobileView/components/atoms/SliderComponent2';
import { initializeSmallCase, updateSmallCaseAuthId } from 'services/smallcase';

const InvestInMintboxes = (props) => {
  const [dashboardContent, setDashboardContent] = React.useState([]);
  const [loading, setLoading] = React.useState(true);
  const [user,setUser] = React.useState();
  const [profiled, setProfiled] = React.useState(false);
  const [isDematLinked, setIsDematLinked] = React.useState(false);
  const [riskScore, setRiskScore] = React.useState(0);
  const [isInvested, setIsInvested] = React.useState(false)

  const investDash = [
    {
      header: "Explore mintboxes",
      description: "",
      content: [
        {
          UID: 'a',
          heading: <>
            <div className="header2dark">All</div>
            <div className="bodyTextLight">mintboxes</div>
          </>,
          btnColor: cssVar.getPropertyValue('--indianEquity'),
          bgColor: cssVar.getPropertyValue('--lowestRisk'),
          onClick: () => {
            props.history.push({
              pathname: "/list",
              state: { type: 3 }
            })
          },
          show: true
        },
        {
          UID: 'b',
          heading: <>
            <div className="header2dark">Watchlisted</div>
            <div className="bodyTextLight">mintboxes</div>
          </>,
          btnColor: cssVar.getPropertyValue('--secondaryGreen'),
          bgColor: cssVar.getPropertyValue('--conservativeRisk'),
          onClick: () => {
            props.history.push({
              pathname: "/list",
              state: { type: 3, query: "Watchlist" }
            })
          },
          show: true
        },
        {
          UID: 'c',
          heading: <>
            <div className="bodyTextLight">Built for</div>
            <div className="header2dark">your risk</div>
          </>,
          btnColor: cssVar.getPropertyValue('--internationalEquity'),
          bgColor: cssVar.getPropertyValue('--moderateRisk'),
          onClick: () => {
            props.history.push({
              pathname: "/list",
              state: { type: 3, query: "Recommended" }
            })
          },
          show: true
        },
      ]
    },
    {
      header: "Sorted by risk level",
      description: "",
      content: [
        {
          UID: 'a',
          heading: <>
            <div className="header2dark">{RiskFormat(1)}</div>
            <div className="bodyTextLight">mintboxes</div>
          </>,
          btnColor: cssVar.getPropertyValue('--indianEquity'),
          bgColor: cssVar.getPropertyValue('--lowestRisk'),
          onClick: () => {
            props.history.push({
              pathname: "/list",
              state: { type: 3, query: "RiskLevel", RISKLEVEL: 1 }
            })
          },
          show: true
        },
        {
          UID: 'b',
          heading: <>
            <div className="header2dark">{RiskFormat(2)}</div>
            <div className="bodyTextLight">mintboxes</div>
          </>,
          btnColor: cssVar.getPropertyValue('--secondaryGreen'),
          bgColor: cssVar.getPropertyValue('--conservativeRisk'),
          onClick: () => {
            props.history.push({
              pathname: "/list",
              state: { type: 3, query: "RiskLevel", RISKLEVEL: 2 }
            })
          },
          show: true
        },
        {
          UID: 'c',
          heading: <>
            <div className="header2dark">{RiskFormat(3)}</div>
            <div className="bodyTextLight">mintboxes</div>
          </>,
          btnColor: cssVar.getPropertyValue('--gold'),
          bgColor: cssVar.getPropertyValue('--moderateRisk'),
          onClick: () => {
            props.history.push({
              pathname: "/list",
              state: { type: 3, query: "RiskLevel", RISKLEVEL: 3 }
            })
          },
          show: true
        },
        {
          UID: 'd',
          heading: <>
            <div className="header2dark">{RiskFormat(4)}</div>
            <div className="bodyTextLight">mintboxes</div>
          </>,
          btnColor: cssVar.getPropertyValue('--peach'),
          bgColor: cssVar.getPropertyValue('--secondHeighestRisk'),
          onClick: () => {
            props.history.push({
              pathname: "/list",
              state: { type: 3, query: "RiskLevel", RISKLEVEL: 4 }
            })
          },
          show: true
        },
        {
          UID: 'e',
          heading: <>
            <div className="header2dark">{RiskFormat(5)}</div>
            <div className="bodyTextLight">mintboxes</div>
          </>,
          btnColor: cssVar.getPropertyValue('--internationalEquity'),
          bgColor: cssVar.getPropertyValue('--highestRisk'),
          onClick: () => {
            props.history.push({
              pathname: "/list",
              state: { type: 3, query: "RiskLevel", RISKLEVEL: 5 }
            })
          },
          show: true
        },
      ]
    },
    {
      header: "Sorted by investment horizon",
      description: "",
      content: [
        {
          UID: 'a',
          heading: <>
            <div className="bodyTextLight">mintboxes</div>
            <div className="header2dark">for 1-3 years</div>
          </>,
          btnColor: cssVar.getPropertyValue('--darkGreen'),
          bgColor: '#ECFBF3',
          onClick: () => {
            props.history.push({
              pathname: "/list",
              state: { type: 3, query: "DurationId", DURATIONID: 1 }
            })
          },
          show: true
        },
        {
          UID: 'b',
          heading: <>
            <div className="bodyTextLight">mintboxes</div>
            <div className="header2dark">for 3-5 years</div>
          </>,
          btnColor: cssVar.getPropertyValue('--secondaryGreen'),
          bgColor: cssVar.getPropertyValue('--conservativeRisk'),
          onClick: () => {
            props.history.push({
              pathname: "/list",
              state: { type: 3, query: "DurationId", DURATIONID: 2 }
            })
          },
          show: true
        },
        {
          UID: 'c',
          heading: <>
            <div className="bodyTextLight">mintboxes</div>
            <div className="header2dark">for 5+ years</div>
          </>,
          btnColor: cssVar.getPropertyValue('--purple'),
          bgColor: '#ECE7FA',
          onClick: () => {
            props.history.push({
              pathname: "/list",
              state: { type: 3, query: "DurationId", DURATIONID: 3 }
            })
          },
          show: true
        },
      ]
    }
  ]
  React.useEffect(async () => {
    await loadData();
    setLoading(false)
  }, []);

  const loadData = async () => {
    try {
      await db.get(db.model.user).then(async (p) => {
        await getClientData(p.userData.id)
        await getDashboardData()
        await getPortfolio(p.userData.id)
        setUser(p.userData)
      });
    } catch (error) {
      toast.error(error);
    }
  };

  const getClientData = async (id) => {
    await db.get(db.model.clientData).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getClient(id).then(res => res);
        if (res.type === true) {
          const a = JSON.parse(res.data);
          let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
          console.log("Get Client API :", aa)
          setProfiled(aa.Client[0].IsProfileComplete === 1)
          setIsDematLinked(aa.SmallcaseInfo.length !== 0)
          setRiskScore(aa.RiskInfo[0].RiskScore || 0)
          db.set(db.model.clientData, JSON.stringify(aa))
        }
      } else {
        setIsDematLinked(p.SmallcaseInfo.length !== 0)
        setProfiled(p.Client[0].IsProfileComplete === 1)
        setRiskScore(p.RiskInfo[0].RiskScore || 0)
      }
    })
  };

  const getDashboardData = async () => {
    await db.get(db.model.dashboard).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getDashboardData().then(res => res);
        if (res.type === true) {
          console.log("Get Dashboard API: ", res.data)
          db.set(db.model.dashboard, JSON.stringify(res.data))
          setDashboardContent(res.data)
        }
      } else {
        setDashboardContent(p)
      }
    })
  }

  const getPortfolio = async (id) => {
    await db.get(db.model.portfolio).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getPortfolio(id).then(res => res);
        if (res.type === true) {
          const a = await JSON.parse(res.data);
          let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
          console.log("Get portfolio API :", aa)
          // setIsInvested(aa.MintboxConsolidated.length + aa.ETFConsolidated.length + aa.FundsConsolidated.length !== 0)
          setIsInvested(aa.PortfolioConsolidated && Number(aa.PortfolioConsolidated[0].PortfolioBalance) !== 0)
          db.set(db.model.portfolio, JSON.stringify(aa))
        }
      } else {
        // setIsInvested(p.MintboxConsolidated.length + p.ETFConsolidated.length + p.FundsConsolidated.length !== 0)
        setIsInvested(p.PortfolioConsolidated && Number(p.PortfolioConsolidated[0].PortfolioBalance) !== 0)
      }
    })
  }


  const NotInvestedSlider = () => {

    const slides = [
      {
        UID: 'a',
        SubTitle: 'Invest using your demat account',
        Description: 'Smart portfolios await. Securely link your demat account to start investing!',
        ActionText: 'Link demat account',
        onClick: async () => {
          const res = await AdminServices.getSmallcaseTransactionId({ "intent": "CONNECT" }).then(rs => rs)
          if (res.type) {
            console.log("Trx id =>>", res.data.data.transactionId)
            if (window.sc === undefined) {
              console.log("Initializing Smallcase")
              await initializeSmallCase();
            }
            await window.sc.triggerTransaction({ transactionId: res.data.data.transactionId })
              .then((a) => {
                console.log(a)
                if (a) {
                  updateSmallCaseAuthId(user.id, a.smallcaseAuthToken, a.broker).then(res => {
                    console.log(res)
                    if (res) {
                      db.remove(db.model.clientData)
                      toast.success('Demat linked')
                    } else {
                      toast.error('Demat already linked with another mintbox account')
                    }
                  })
                }
              })
              .catch((p) => {
                if(p.message === 'user_cancelled'){
                  return
                } else {
                  toast.error('Something went wrong! Try again.')
                }
              });
          }
        },
        Theme: 'DARKGREEN',
        URL: 'https://d3etfstcuyoos5.cloudfront.net/app-images/final_imgs/link_demat.png',
        show: !isDematLinked
      },
      {
        UID: 'b',
        SubTitle: 'Don\'t have a demat? Open one now!',
        Description: 'Enter the world of stock markets by openeing your first demat account',
        ActionText: 'Open demat account',
        onClick: async () => {
          if (window.sc === undefined) {
            console.log("Initializing Smallcase")
            await initializeSmallCase();
          }
          window.sc.signup()
            .then(
              () => {
                console.log("signup flow has now ended");
              }
            );
        },
        Theme: 'GREEN',
        URL: 'https://d3etfstcuyoos5.cloudfront.net/app-images/final_imgs/open_demat.png',
        show: !isDematLinked
      },
      // {
      //   UID: 'd',
      //   SubTitle: 'Let\'s find you some mintboxes!',
      //   Description: 'Answer a few questions and we will recommend you a few mintboxes',
      //   ActionText: <>Get started <AiOutlineRight size={12} style={{ marginLeft: '4px' }} /></>,
      //   onClick: () => { props.history.push("/risk-profile") },
      //   Theme: 'DARKGREEN',
      //   URL: 'https://d3etfstcuyoos5.cloudfront.net/app-images/final_imgs/perfect_mintbox.png',
      //   show: isDematLinked
      // }
    ]
    return <SliderComponent2 navigation={props.history} type="boxCard" component={{ 'content': slides.filter(f => f.show) }} />
  }

  const displaySection = (section) => {
    switch (section.sectionType) {
      case 'header_sec': {
        return <NotInvestedSlider />
      }
      case 'video_slider': {
        return <SliderComponent navigation={props.history} type="videoView" section={section} />
      }
      case 'recently_view': {
        return <DisplayRecentlyViewed navigation={props.history} type={3} header="Recently viewed mintboxes" />
      }
      default:{
        return <></>
      }
    }
  }

  if (loading) {
    return (
      <div style={{ flex: 1 }}>
        <MainHeader
          header="Invest in mintboxes"
          right="help"
          screenType="Invest"
          navigation={props.history}
        />
        <ActivityLoaderMintbox />
      </div>
    );
  } else return (
    <div className='mainContainer'>
      <MainHeader
        header="Invest in mintboxes"
        navigation={props.history}
      />
      <div className="dataContainer px-0">
        <div className='searchBar' style={{marginBottom:'0'}}
          onClick={() => { props.history.push('Invest/Search/Mintboxes') }}>
          <FiSearch size={20} style={{ margin: "0 4%", color: cssVar.getPropertyValue("--secondaryGreen") }} />
          <div className='searchInput'>Search by mintbox name</div>
        </div>
        {
          isInvested ?
            dashboardContent?.filter(c => c.stageName === 'LINKED_DEMAT')[0]?.dashboardSection.slice(0,2).map(section => {
              return displaySection(section)
            })
            :
              dashboardContent?.filter(c => c.stageName === 'NEW_USER')[0]?.dashboardSection.slice(0,3).map(section => {
                return displaySection(section)
              })
        }
        {
          investDash.map((component, i) => {
            return <SliderComponent2 navigation={props.history} type="smallBox" component={component} key={i} />
          })
        }
      </div>
      <BottomTabs />
    </div>
  )
}

export default InvestInMintboxes
